import * as React from "react"
import { Link, graphql } from "gatsby"
import HosthustleLayout from "../components/layout/Layout"
import Seo from "../components/seo/Seo"
import PostCard from "../components/blog/PostCard"

const CategoryTemplate = ({ pageContext, data }) => {
  const { category } = pageContext
  const posts = data.allMdx.nodes

  return (
    <HosthustleLayout>
      <div className="max-w-[1200px] mx-auto px-4 py-10">
        <header className="mb-6 border-b border-[var(--color-editorial-border)] pb-4">
          <h1
            className="text-[var(--color-editorial-black)]"
            style={{
              fontSize: "var(--text-3xl)",
              fontWeight: 600,
              lineHeight: 1.2,
            }}
          >
            Category: {category}
          </h1>
        </header>
        <div className="space-y-4">
          {posts.map(post => (
            <Link key={post.fields.slug} to={post.fields.slug}>
              <PostCard
                variant="small"
                image={post.frontmatter.cover}
                category={post.frontmatter.category}
                title={post.frontmatter.title}
                author="Hosthustle"
                date={post.frontmatter.date}
              />
            </Link>
          ))}
        </div>
      </div>
    </HosthustleLayout>
  )
}

export const Head = ({ pageContext }) => {
  const { category } = pageContext
  return <Seo title={`Posts in category "${category}"`} />
}

export default CategoryTemplate

export const pageQuery = graphql`
  query($category: String!) {
    allMdx(
      filter: { frontmatter: { category: { eq: $category } } }
      sort: { frontmatter: { date: DESC } }
    ) {
      nodes {
        fields {
          slug
        }
        frontmatter {
          title
          date(formatString: "MMMM DD, YYYY")
          description
          cover
          coverAlt
          category
        }
      }
    }
  }
`
